@import "src/scss/settings/fonts";
@import "src/scss/settings/colors";

.homeAboutBox {
  width: 1900px;
  height: 935px;
  margin-top: 80px;
  display: flex;
  margin-bottom: 70px;

  &_content {
    background-color: $section-color;
    width: 958px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__title {
      font-family: $font-base;
      font-size: 38px;
      letter-spacing: -0.76px;
      line-height: 55px;
      color: $text-color;
    }

    &__img {
      margin-top: 26px;
    }

    &__text {
      margin-top: 55px;
      width: 656.9px;
      height: 208.91px;
      font-family: $font-base;
      font-size: 30px;
      letter-spacing: -0.6px;
      line-height: 54px;
      color: $text-color;
      text-align: center;
    }

    &__signature {
      margin-top: 40.09px;
      margin-left: 441.75px;
    }
  }

  &_image {
    width: 942px;
    height: 100%;
  }
}